.bui-vsc-statusbar {
	display: -ms-flexbox;
	display: flex;
	height: 24px;
	-ms-flex-flow: row nowrap;
	    flex-flow: row nowrap;
	padding: 0 16px;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	border-radius: 0 0 4px 4px;
	font-size: 10px;
	border-top: 1px solid;
}

.bui-vsc-statusbar svg {
	vertical-align: middle;
	display: inline;
	height: 14px;
	width: 14px;
	fill: currentColor;
}

.bui-vsc-statusbar__git {
	margin-right: auto;
}

.bui-vsc-statusbar__lang {
	margin-left: auto;
}



